/* Сбрасываем верхние margin */
h1,
h2,
h3,
h4,
ul,
p {
  margin-top: 0px;
  margin-bottom: 0px;
}

/* Список без маркеров */
.list {
  list-style: none;
  padding-left: 0px;
}

/* Ссылки без подчеркивания */
.link {
  text-decoration: none;
}

img {
  display: block;
  width: 100%;
  height: auto;
}
